import "./post.css";
import MoreVertIcon from "@mui/icons-material/MoreVert";
// import { Users } from "../../dummyData";
import { useContext, useEffect, useState } from "react";
import axios from "axios";
import { format } from "timeago.js";
import { Link } from "react-router-dom";
import { AuthContext } from "../../contex/AuthContext";
const PF = process.env.REACT_APP_PUBLIC_FOLDER;

export default function Post({ post }) {
  const [like, setLike] = useState(post.likes.length);
  const [liked, setLiked] = useState(false);
  const [user, setUser] = useState({});
  const { user: currentUser } = useContext(AuthContext);
  // console.log(currentUser);

  useEffect(() => {
    const fetchUser = async () => {
      const res2 = await axios.get(`/api/users/?userId=${post.userId}`);
      // console.log(res2.data);
      try {
        setUser(res2.data);
      } catch (err) {
        console.log(err);
      }
    };
    fetchUser();
  }, [post.userId]);
  // console.log(user);

  const likeHandeler = () => {
    try {
      axios.put("/api/posts/" + post._id + "/like", {
        userId: currentUser._id,
      });
    } catch (err) {}
    setLike(liked ? like - 1 : like + 1);
    setLiked(!liked);
  };
  console.log(post);

  return (
    <div className="post">
      <div className="postWrapper">
        <div className="postTop">
          <div className="postTopLeft">
            <Link to={`profile/${user.username}`}>
              <img
                className="postProfileImg"
                src={
                  user.profilePicture
                    ? user.profilePicture
                    : PF + "/assets/person/noAvatar.png"
                }
                alt="profilepicture"
              />
            </Link>
            <span className="postUsername">{user.username}</span>
            <span className="postDate">{format(post.createdAt)}</span>
          </div>
          <div className="postTopRight">
            <MoreVertIcon />
          </div>
        </div>
        .
        <div className="postCentr">
          <h2 style={{ color: "green" }} className="postText">
            {post?.desc}
          </h2>
          <br />
          <br />
          <br />
        </div>
        <div className="postBottom">
          <div className="postVottomLeft">
            <img
              src="assets/like.png"
              className="likeIcon"
              onClick={likeHandeler}
              alt="likeImage"
            />
            <img
              src="assets/heart.png"
              className="likeIcon"
              onClick={likeHandeler}
              alt="likeImage"
            />
            <span className="postLikeCounter">{like} people like it</span>
          </div>
          <div className="postBottomRight">
            <span className="postCommentText">{post.comment} comments</span>
          </div>
        </div>
      </div>
    </div>
  );
}
