import "./profileUpdate.css";
import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../contex/AuthContext";

export default function ProfileUpdate() {
  const [user, setUser] = useState({});
  const [image, setImage] = useState("");
  const [editValue, setEditValue] = useState({
    city: user.city,
    country: user.form,
    desc: user.desc,
    // relationship: "",
  });
  const [updateData, setupdateData] = useState("");
  const { user: currentUser } = useContext(AuthContext);

  ////////get user//////
  useEffect(() => {
    const fetchUser = async () => {
      const res = await axios.get(
        `/api/users?username=${currentUser.username}`
      );

      setUser(res.data);
    };
    fetchUser();
  }, [currentUser.username]);

  ////////////submit form/////////

  const submitform = async (e) => {
    e.preventDefault();
    setupdateData("please wait");

    const res2 = await axios.put("/api/users/update", {
      currentUser: currentUser._id,
      city: editValue.city,
      country: editValue.country,
      desc: editValue.desc,
      // relationship: value.relationship,
    });
    setupdateData(res2.data);
  };

  const showEditProPic = (file) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      console.log(image);
      setImage(reader.result);
    };
  };

  const fromHandel = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setEditValue({ ...editValue, [name]: value });
  };

  return (
    <>
      <form className="form" onSubmit={submitform}>
        <label htmlFor="desc">About you:</label>
        <textarea
          id="desc"
          className="textareaInput"
          name="desc"
          onChange={(e) => fromHandel(e)}
          value={editValue.desc}
        ></textarea>
        <label htmlFor="city">city:</label>
        <input
          type="text"
          className="cityInput"
          id="city"
          name="city"
          value={editValue.city}
          onChange={(e) => fromHandel(e)}
        />
        <label htmlFor="country">Country:</label>
        <input
          type="text"
          className="countryInput"
          id="country"
          name="country"
          value={editValue.country}
          onChange={(e) => fromHandel(e)}
        />
        <label htmlFor="relation">Relationship:</label>
        <select
          id="relation"
          className="relationshipselector"
          value={editValue.relationship}
          name="relationship"
          onChange={(e) => fromHandel(e)}
        >
          <option value="2">Unmarried</option>
          <option value="1">Married</option>
        </select>
        {/* <span>Gender:</span>
        <label htmlFor="genderMale">Male</label>
        <input type="radio" id="genderMale" name="gender" value="male" />
        <label htmlFor="female">Female</label>
        <input type="radio" id="female" name="gender" value="female" />
        <label htmlFor="other">Other</label>
        <input type="radio" id="other" name="gender" value="other" /> */}

        <br />
        <br />

        <button className="allBtn" type="submit">
          Submit
        </button>
        <p>{updateData}</p>
      </form>
    </>
  );
}
