import "./profile.css";
import Topbar from "../../components/topbar/Topbar";
import Sidebar from "../../components/sidebar/Sidebar";
import Feed from "../../components/feed/Feed";
import Rightbar from "../../components/rightbar/Rightbar";
import ProfilePicUpdate from "../../components/profilePicUpdate/ProfilePicUpdate";
import ProfileUpdate from "../../components/profileUpdate/ProfileUpdate";
import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { AuthContext } from "../../contex/AuthContext";
import CloseIcon from "@mui/icons-material/Close";

export default function Profile() {
  const username = useParams().username;
  const PF = process.env.REACT_APP_PUBLIC_FOLDER;

  ///////////useState//////////////
  const [user, setUser] = useState({});
  let [click, setClick] = useState(false);
  const [click2, setClick2] = useState(false);
  const { user: currentUser } = useContext(AuthContext);

  /////////fetch user details from api//////////////
  useEffect(() => {
    const fetchUser = async () => {
      const res = await axios.get(`/api/users?username=${username}`);

      setUser(res.data);
    };
    fetchUser();
  }, [username]);

  ////////////image update/////////////

  const editProfilePicHandel = () => {
    setClick(true);
  };

  let showArr = ["editInfoContainer"];
  let showArr2 = ["editInfo"];
  if (click) {
    showArr.push("openEdit");
    showArr2.push("openEdit");
    // setClick(false);
  }

  const closeArr = () => {
    // setClick(false);
    // setClick2(false);
    showArr.pop("openEdit");
    showArr2.pop("openEdit");
    showArr3.pop("openEdit");

    window.location.reload();
  };
  //////////////profile update//////////////
  const showArr3 = ["editInfo"];
  const editProfileHandel = () => {
    setClick2(true);
  };
  if (click2) {
    showArr.push("openEdit");
    showArr3.push("openEdit");
  }

  return (
    <>
      <div className="container">
        <Topbar />
        <div className="profile">
          <Sidebar />
          <div className="profileRight">
            <div className="profileRightTop">
              <div className="profileCover">
                <img
                  className="profileCoverImg"
                  src={
                    user.coverPicture
                      ? user.coverPicture
                      : "data:image/png;base64,"
                  }
                  alt="profile-pic"
                />
                <img
                  className="profileUserImg"
                  src={
                    user.profilePicture
                      ? user.profilePicture
                      : " data:image/png;base64,"
                  }
                  alt="profile-pic"
                />
                <div className="editButton">
                  {user.username == currentUser.username && (
                    <button
                      className="rightbarFollowButton"
                      onClick={editProfilePicHandel}
                    >
                      Edit profile picture
                    </button>
                  )}

                  {user.username == currentUser.username && (
                    <button
                      className="rightbarFollowButton"
                      onClick={editProfileHandel}
                    >
                      Edit profile
                    </button>
                  )}
                </div>
              </div>
              <div className="profileInfo">
                <h4 className="profileInfoName">{user.username}</h4>
                <span className="profileInfoDesc">{user.desc}</span>
              </div>
            </div>
            <div className="profileRightBottom">
              <Feed key={Date()} username={username} />

              <Rightbar key={user._id} user={user} />
            </div>
          </div>
        </div>
      </div>

      {/* <div className="editInfoContainer  "> */}
      <div className={showArr.join(" ")}>
        <div className={showArr2.join(" ")}>
          {/* <div className="editInfo"> */}
          <ProfilePicUpdate />
          <button className="closeBtn" onClick={closeArr}>
            <CloseIcon />
          </button>
        </div>
        <div className={showArr3.join(" ")}>
          <ProfileUpdate />
          <button className="closeBtn" onClick={closeArr}>
            <CloseIcon />
          </button>
        </div>
      </div>
    </>
  );
}
