import axios from "axios";
import React, { useContext, useState } from "react";
import { AuthContext } from "../../contex/AuthContext";

export default function ProfileUpdate() {
  const [image, setImage] = useState("");

  const [updateData, setupdateData] = useState("");
  const { user: currentUser } = useContext(AuthContext);
  // console.log(currentUser._id);

  const submitform = async (e) => {
    e.preventDefault();
    setupdateData("please wait");

    if (image) {
      const update = await axios.put("/api/users/updateDp", {
        currentUser: currentUser._id,
        imageUrl: image,
      });
      await setupdateData(update.data);
    }
  };

  const showEditProPic = (file) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      console.log(image);
      setImage(reader.result);
    };
  };

  const profilePicEvent = (e) => {
    const file = e.target.files[0];
    showEditProPic(file);
  };

  return (
    <>
      <form onSubmit={submitform}>
        <h1 className="H1" id="updateProfilePic">
          Upload your new profile picture
        </h1>

        <br />
        <input
          type="file"
          id="updateProfilePic"
          className="updateProfilepic "
          onChange={(e) => {
            profilePicEvent(e);
          }}
        />
        <img
          className="profilePicEditImage"
          style={{
            height: "200px",
            width: "200px",
            objectFit: "cover",
            borderRadius: "50%",
          }}
          src={image}
        />

        <button className="allBtn" type="submit">
          Submit
        </button>
        <p>{updateData}</p>
      </form>
    </>
  );
}
