import { useContext, useRef, useState } from "react";
// import "./login.css";
import { LoginCall, loginCall } from "../../apiCalls";
import { AuthContext } from "../../contex/AuthContext";
import CircularProgress from "@mui/material/CircularProgress";
import { Link } from "react-router-dom";

export default function Login() {
  const email = useRef();
  const password = useRef();
  const { user, isFetching, dispatch } = useContext(AuthContext);
  const [err, setErr] = useState("");

  const submitHandeler = (e) => {
    e.preventDefault();
    // console.log(email.current.value);
    // console.log(password.current.value);
    LoginCall(
      { email: email.current.value, password: password.current.value },
      dispatch
    );
    setTimeout(() => {
      setErr("username or password incorrect");
    }, 2000);
    // console.log(res.data);
  };
  // console.log(user);
  return (
    <div className="login">
      <div className="loginWrapper">
        <div className="loginLeft">
          <h3 className="loginLogo">facebook</h3>
          <span className="loginDesc">
            Connect with friends and the world around you on facebook.
          </span>
        </div>
        <div className="loginRight">
          <form className="loginBox" onSubmit={submitHandeler}>
            <input
              type="email"
              placeholder="Email"
              className="logininput"
              ref={email}
              required
            />

            <input
              type="password"
              placeholder="Password"
              className="logininput"
              ref={password}
              required
            />
            <p className="loginErr">{err}</p>
            <button className="loginButton" disabled={isFetching}>
              {isFetching ? <CircularProgress color="secondary" /> : " Log In"}
            </button>
            <span className="loginForgot">Forgot password</span>
            <Link to={"/register"}>
              <button className="loginRegisterButton">
                Create a new account
              </button>
            </Link>
          </form>
        </div>
      </div>
    </div>
  );
}
