import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import "./topbar.css";
import SearchIcon from "@mui/icons-material/Search";
// import { SearchIcon, PersonIcon } from "@mui/icons-material";
import { AuthContext } from "../../contex/AuthContext";
import PersonIcon from "@mui/icons-material/Person";
import ChatIcon from "@mui/icons-material/Chat";
import NotificationsIcon from "@mui/icons-material/Notifications";
import { Link, Navigate } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import LogoutIcon from "@mui/icons-material/Logout";
import DehazeIcon from "@mui/icons-material/Dehaze";
import CloseIcon from "@mui/icons-material/Close";

export default function Topbar() {
  const { user } = useContext(AuthContext);
  const PF = process.env.REACT_APP_PUBLIC_FOLDER;
  const navigate = useNavigate();
  const [currentUser, setCurrentUser] = useState({});
  const [openLinkArrSrate, setOpenLinkArrState] = useState(["topbarOpen"]);
  const [openState, setOpenState] = useState(["openIcon"]);
  const [closeState, setCloseState] = useState(["closeIcon"]);

  const handelClick1 = () => {
    localStorage.clear();

    navigate("/register");
    window.location.reload();
  };

  ////////get user//////
  useEffect(() => {
    const fetchUser = async () => {
      const res = await axios.get(`/api/users?username=${user.username}`);

      setCurrentUser(res.data);
    };
    fetchUser();
  }, [currentUser.username]);

  //////////openHandel/////

  // const openLinkArr = ["topbarOpen"];
  const openHandel = () => {
    // alert("ok");
    if (!openLinkArrSrate.includes("show")) {
      setOpenLinkArrState(["topbarOpen", "show"]);
      setCloseState(["closeIcon", "show"]);
      setOpenState(["hide", "openIcon"]);
    } else if (openLinkArrSrate.includes("show")) {
      setOpenLinkArrState(["topbarOpen"]);
      setCloseState(["closeIcon", "hide"]);
      setOpenState(["show", "openIcon"]);
    }
  };

  ///////////

  return (
    <div className="topbarContainer">
      <div className="topbarLeft">
        <Link to="/">
          <span className="topbaricon">facebook</span>
        </Link>
      </div>
      <div className="topbarCenter">
        <div className="searchbar">
          <SearchIcon className="searchIcon" />
          <input
            type="text"
            placeholder="Search for friend, post or video"
            className="searchInput"
          />
        </div>
      </div>

      <div className="topbarRight">
        <div className="topbarLinks">
          <Link
            to="/friends"
            style={{ textDecoration: "none", color: "white" }}
          >
            <span className="topbarLink">Find friends</span>
          </Link>

          <Link to="/" style={{ textDecoration: "none", color: "white" }}>
            <span className="topbarLink">Homepage</span>
          </Link>
          <Link
            to={"/profile/" + currentUser.username}
            style={{ textDecoration: "none", color: "white" }}
          >
            <span className="topbarLink">Profile</span>
          </Link>
        </div>
        <div className="topbarIcons">
          <div className="topbarIcoItem">
            <PersonIcon />
            <span className="topbarIconBadge">1</span>
          </div>
          <div className="topbarIcoItem">
            <ChatIcon />
            <span className="topbarIconBadge">1</span>
          </div>
          <div className="topbarIcoItem">
            <NotificationsIcon />
            <span className="topbarIconBadge">1</span>
          </div>
        </div>
        <Link to={"/profile/" + currentUser.username}>
          <img
            src={
              currentUser.profilePicture
                ? currentUser.profilePicture
                : "data:image/png;base64,"
            }
            alt=""
            className="topbarImg"
          />
        </Link>

        <span className="logoutBtn" onClick={handelClick1}>
          <LogoutIcon /> <p className="logout">Logout</p>
        </span>

        <button className="topBarOpenIcon" onClick={openHandel}>
          <div className={openState.join(" ")}>
            <DehazeIcon />
          </div>
          <div className={closeState.join(" ")}>
            <CloseIcon />
          </div>
        </button>

        {/* <ul className="topbarOpen show"> */}
        <ul className={openLinkArrSrate.join(" ")}>
          <Link
            to="/friends"
            style={{ textDecoration: "none", color: "white" }}
          >
            <li className="topbarOpenLink">Find friends</li>
          </Link>
          <Link to="/" style={{ textDecoration: "none", color: "white" }}>
            <li className="topbarOpenLink">Homepage</li>
          </Link>
          <Link
            to={"/profile/" + currentUser.username}
            style={{ textDecoration: "none", color: "white" }}
          >
            <li className="topbarOpenLink">Profile</li>
          </Link>
          <button className=" topbarOpenLink" onClick={handelClick1}>
            Logout
          </button>
        </ul>
      </div>
    </div>
  );
}
